import React from 'react';
import ReactDOM from 'react-dom';
import { Mod } from './components/mod';
import { Readme } from './components/readme';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import { Loader } from './components/loader';
import { getMods } from './logic/loadData';

// let mods = ['KeviTV/MekanisM', 'Anuken/ExampleMod', 'ThatOneBepis/Infinitodustry'];

// function Mods() {
//   return mods.map(e => <Mod key={e} mod={e} />);
// }

export default function App() {
  return (
    <BrowserRouter>
      <Route
        exact
        path='/mods'
        component={() => (
          <Loader
            load={() => getMods()}
            component={mods => mods.map(e => <Mod key={e} mod={e} />)}
          />
        )}
      />
      <Route
        exact
        path='/mod/:author/:name'
        component={({ match }) => (
          <React.Fragment>
            <Mod mod={match.params.author + '/' + match.params.name} showLearnMore={false} />
            <Readme mod={match.params.author + '/' + match.params.name} />
          </React.Fragment>
        )}
      />
      <Route exact path='/'>
        <Redirect to='/mods' />
      </Route>
    </BrowserRouter>
  );
}

ReactDOM.render(<App />, document.getElementById('app'));
